
.card.gem-diamond {
  background-color: #e8e8e8;
  border-color: #bfbfbf;
}
.card.gem-sapphire {
  background-color: #a2c0fc;
  border-color: #7999d9;
}
.card.gem-emerald {
  background-color: #87e093;
  border-color: #3a9c47;
}
.card.gem-ruby {
  background-color: #f59a9a;
  border-color: #d46363;
}
.card.gem-opal {
  background-color: #595959;
  border-color: #1a1a1a;
}


.card-cost {
  width: 0.6rem;
  height: 1rem;
  padding-left: 0.2rem;
  padding-right: 0.2em;
  border: 2px solid black;
  border-radius: 100px;
}

.card-cost-diamond {
  background-color: #e8e8e8;
  border-color: #bfbfbf;
}
.card-cost-sapphire {
  background-color: #a2c0fc;
  border-color: #7999d9;
}
.card-cost-emerald {
  background-color: #87e093;
  border-color: #3a9c47;
}
.card-cost-ruby {
  background-color: #f59a9a;
  border-color: #d46363;
}
.card-cost-opal {
  color: white;
  background-color: #595959;
  border-color: #1a1a1a;
}


.chippool {
  padding-top: 0.5rem;
  width: 5rem;
  height: 5rem;
  border: black;
  border-radius: 100px;
}

.chippool-btn {
  width: 100%;
  height: 100%;
  border-radius: 100px;
  background: none;
  border: solid 3px black;
  font-size: 2rem;
  font-weight: 700;
  -webkit-text-stroke: 1px white;
  cursor: grabbing;
}
.chippool-btn:disabled {
  cursor: not-allowed;
  filter: brightness(75%);
}


.diamond-pool {
  background-color: #e8e8e8;
  border-color: #bfbfbf;
}
.sapphire-pool {
  background-color: #a2c0fc;
  border-color: #7999d9;
}
.emerald-pool {
  background-color: #87e093;
  border-color: #3a9c47;
}
.ruby-pool {
  background-color: #f59a9a;
  border-color: #d46363;
}
.opal-pool {
  color: white;
  background-color: #595959;
  border-color: #1a1a1a;
}
.gold-pool {
  background-color: #fff8a6;
  border-color: #e8b32e;
}


.card {
  position: relative;
  width: 7rem;
  height: 12rem;
  border-radius: 1vw;
  border: 0.3em solid;
  margin: 0.2em;
}

.card .card-points {
  position: absolute;
  left: 0.1rem;
  margin: 0.15em 0 0 0.25em;
  font-size: 2rem;
  font-weight: 700;
  -webkit-text-stroke: 1px white;
}
.card .card-gem {
  position: absolute;
  right: 0.1rem;
  margin: 0.5em 0.5em 0 0;
  width: 2rem;
}

.card-cost-div {
  position: absolute;
  display: flex;
  bottom: 0;
  flex-direction: column;
}

.card {
  pointer-events: none;
}

.card-btn {
  position: absolute;
  padding: 0;
  margin: 0;
  border: 0;
  width: 100%;
  height: 100%;
  background: none;
  cursor: grabbing;
  pointer-events: auto;
}

.card-btn:disabled {
  cursor: not-allowed;
  pointer-events: none;
  background: rgba(0,0,0,0.2);
}

.card:hover {
  filter: brightness(80%);
  transition: 0.2s;
}

#mode-btn {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  background-color: rgba(0,0,0,0);
  border: 2px solid #e8b32e;
  border-radius: 0.3rem;
  background-color: #fff8a6;
}
#take-chips {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  background-color: rgba(0,0,0,0);
  border: 2px solid #e8b32e;
  border-radius: 0.3rem;
  background-color: #fff8a6;
}
.chippool-active {
  box-shadow: 1px 1px 5px 1px black;
}

#table {
  width: 100% !important;
  margin: 0;
}
table {
  width: 100%;
}
